<template>
    <div class="baseRegExp-modal">
        <a-modal :title="title" :visible="regVisible" :mask=false :maskClosable=false @cancel="handleCancel"  :width="600" class="modalCls">
            <a-form-model ref="baseRegForm" :model="baseRegForm" :rules="rulesForm">
                <a-form-model-item label="正则名称" prop="name">
                    <a-input v-model="baseRegForm.name" placeholder="请输入名称"></a-input>
                </a-form-model-item>
                <a-form-model-item label="正则分类" prop="category">
                    <a-input v-model="baseRegForm.category" placeholder="请输入分类"></a-input>
                </a-form-model-item>
                <a-form-model-item label="正则表达式" prop="regex">
                    <a-input v-model="baseRegForm.regex" placeholder="请输入正则表达式"></a-input>
                </a-form-model-item>
                <a-form-model-item label="是否启用" prop="isEnabled">
                    <a-switch v-model="baseRegForm.isEnabled"></a-switch>
                </a-form-model-item>
                <a-form-model-item label="正则描述" prop="description">
                    <a-textarea v-model="baseRegForm.description" placeholder="请输入正则描述信息" rows="4"></a-textarea>
                </a-form-model-item>

            </a-form-model>
            <template slot="footer">
                <a-button type="link" ghost @click="handleCancel">取消</a-button>
                <a-button type="primary" @click="handleOk">提交</a-button>
            </template>

        </a-modal>

    </div>
</template>

<script>
  export default {
    name: "baseRegExp-modal",
    props:{
      title:{
        type: String,
        default: "添加/编辑正则",
      },
      regVisible:{
        type:Boolean,
        default: false,
        required: true,
      },
      // 0为添加，1为编辑
      regType:{
        type:Number,
        default:0,
        required: true,
      },
      formContent: {
        type:Object,
        default:function () {
          return{
            name:'',
            category:'',
            regex:'',
            description:'',
            isEnabled:false,
            creator:'',
            gmtCreate:'',
            gmtModified:'',
          }
        }
      }


    },
    data(){
      return{
        baseRegForm:{
          name:'',
          category:'',
          regex:'',
          isEnabled:false,
          description:''
        },
        rulesForm:{
          name:[{ required: true, message: "请输入正则名称",trigger:'blur'}],
          category:[{required: true,message:'请输入正则类型',trigger: 'blur'}],
          regex:[{required: true,message:'请输入正则表达式',trigger: 'blur'}]
        },
      }

    },
    methods:{
      handleOk(){
        this.$refs.baseRegForm.validate(valid =>{
          if (valid){
            // this.baseRegForm.isEnabled = this.baseRegForm.isEnabled === true ? 1:0;
            let form = Object.assign({},this.baseRegForm,{isEnabled:this.baseRegForm.isEnabled?1:0})
            this.$emit('handleOk',{formData:form,regType:this.regType})
          } else {
            this.$message.warning('输入有误，请检查');
            return false
          }
        })
      },
      handleCancel(){
        this.$emit('handleCancel','点击取消')
      }


    },
    watch:{
      formContent:{
        handler:function(newVal){
          Object.assign(this.baseRegForm,newVal);
        },
        deep:true
      }
    }
  }
</script>

<style scoped>

</style>