<template>
    <div class="baseRegExp pageSpace h100">
        <my-breadcrumb></my-breadcrumb>
        <div class="pageContent">
            <a-layout-content>
                <div class="baseRegExp">
                    <div class="tableTopTools clearfix">
                        <p class="toolName fl">工具栏：</p>
                        <div class="buttons fl">
                            <a-button type="primary" @click="addBaseReg">
                                <a-icon type="plus"></a-icon>
                                添加基本正则
                            </a-button>
                        </div>
                    </div>
                    <div class="tableContent">
                        <a-table :columns="columns" :data-source="tableData" rowKey="id" :pagination="false">
                            <span slot="action" slot-scope="text,record">
                                 <a class="t-optColor-normal" @click="editBaseReg(record)">编辑</a>
                                 <a class="t-optColor-warning" @click="deleteBaseReg(record)">删除</a>
                            </span>
                            <span slot="isEnabled" slot-scope="text,record">
                                  <a-switch @click="switchChange(record)" :checked="record.isEnabled"
                                            size="small"></a-switch>
                            </span>
                            <span slot="gmtCreate" slot-scope="text,record">
                                <span>{{record.gmtCreate | dateFormatter(1)}}</span>
                            </span>
                            <span slot="gmtModified" slot-scope="text,record">
                                <span>{{record.gmtModified | dateFormatter(1)}}</span>
                            </span>
                        </a-table>
                        <div class="ar" style="margin-top: 20px">
                            <a-pagination :v-model="curPage" :total="total" :page-size="pageSize"
                                          @change="pageChange"></a-pagination>

                        </div>
                    </div>
                    <baseRegExpModal :regVisible="regVisible" :regType="regType" :formContent="formContent"
                                     @handleOk="handleOk"
                                     @handleCancel="handleCancel">

                    </baseRegExpModal>

                </div>

            </a-layout-content>

        </div>
    </div>
</template>
<script>
    import baseRegExpModal from '@/components/myModal/baseRegExp-modal'

    export default {
        name: 'baseRegExp',
        components: {baseRegExpModal},
        data() {
            return {
                curPage: 1,
                total: 0,
                pageSize: 10,
                regVisible: false, // 模态框状态
                regType: 0,  // 添加或编辑状态

                columns: [
                    {
                        dataIndex: 'name',
                        title: '正则名称'
                    },
                    {
                        dataIndex: 'category',
                        title: '正则分类'
                    },
                    {
                        dataIndex: 'regex',
                        title: '正则表达式',
                        ellipsis: true,
                    },
                    {
                        dataIndex: 'description',
                        title: '正则描述',
                        ellipsis: true
                    },
                    {
                        dataIndex: 'isEnabled',
                        title: '是否启用',
                        scopedSlots: {customRender: 'isEnabled'}
                    },
                    {
                        dataIndex: 'creator',
                        title: '创建者'
                    },
                    {
                        dataIndex: 'gmtCreate',
                        title: '创建时间',
                        scopedSlots: {customRender: 'gmtCreate'}
                    },
                    {
                        dataIndex: 'gmtModified',
                        title: '更新时间',
                        scopedSlots: {customRender: 'gmtModified'}
                    },
                    {
                        dataIndex: 'action',
                        title: "操作",
                        // width:'12%',
                        scopedSlots: {customRender: "action"},
                    }


                ],
                tableData: [],
                formContent: {
                    name: '',
                    category: '',
                    regex: '',
                    description: '',
                    isEnabled: '',
                    creator: '',
                    gmtCreate: '',
                    gmtModified: '',
                },
            }
        },
        methods: {
            // 开关变化
            switchChange(row) {
                let obj = Object.assign({}, {id: row.id, isEnabled: !row.isEnabled === true ? 1 : 0});
                this.$API.updateBasicRegex(obj).then(res => {
                    if (res.code === 1000) {
                        this.$message.success('修改成功');
                        this.getBaseRegExpList()
                    } else {
                        this.$message.warning('修改失败')
                    }
                })
            },
            // 查询
            getBaseRegExpList() {
                let params = {
                    page: this.curPage,
                    size: this.pageSize,
                };
                this.$API.getBasicRegexList(params).then(res => {
                    if (res.code === 1000) {
                        res.data.forEach(list => {
                            list.isEnabled = list.isEnabled === 1 ? true : false;
                        });
                        this.tableData = res.data;
                        this.total = res.total;
                    } else {
                        this.tableData = [];
                        this.$message.warning('暂无数据')
                    }
                })

            },
            // 添加
            addBaseReg() {
                this.resetContent();
                this.regVisible = true;
                this.regType = 0;

            },
            // 提交
            handleOk(val) {
                if (val.regType) {
                    this.$API.updateBasicRegex(Object.assign({}, val.formData, {id: this.formContent.id})).then(res => {
                        if (res.code === 1000) {
                            this.$message.success('基本正则更新成功');
                            this.regVisible = false;
                            this.getBaseRegExpList()
                        } else {
                            this.$message.warning('基本正则更新失败')
                        }
                    })
                } else {
                    this.$API.addBasicRegexList(val.formData).then(res => {
                        if (res.code === 1000) {
                            this.$message.success("基本正则添加成功");
                            this.regVisible = false;
                            this.getBaseRegExpList()
                        } else {
                            this.$message.warning('基本正则添加失败')
                        }
                    })

                }


            },
            // 取消
            handleCancel() {
                this.regVisible = false;
                this.resetContent()
            },

            // 编辑
            editBaseReg(column) {
                this.formContent = column;
                this.regType = 1;
                this.regVisible = true;
            },
            // 删除
            deleteBaseReg(row) {
                const that = this;
                if (row.isEnabled === true) {
                    that.$confirm({
                        title: '确认删除对话框',
                        okText: '确认',
                        cancelText: '取消',
                        content: '该条数据正在使用，确定要删除吗',
                        onOk() {
                            that.$API.deleteBaseRegex(row.id).then(res => {
                                if (res.code === 1000) {
                                    that.$message.success('删除成功');
                                    that.getBaseRegExpList()
                                } else {
                                    that.$message.warning('删除失败')
                                }
                            })
                        }

                    })
                } else {
                    that.$confirm({
                        title: '确认删除对话框',
                        content: () => < div
                    style = "color: red" > 删除后将不可恢复 < /div>,
                    okText:'确认删除',
                        cancelText
                :
                    '取消删除',
                        onOk()
                    {
                        that.$API.deleteBaseRegex(row.id).then(res => {
                            if (res.code === 1000) {
                                that.$message.success('删除成功');
                                that.getBaseRegExpList()
                            } else {
                                that.$message.warning('删除失败')
                            }
                        })
                    }
                })
                }

            },
            pageChange() {
                this.getBaseRegExpList()
            },
            // 重置form表单
            resetContent() {
                this.formContent = {
                    name: '',
                    category: '',
                    regex: '',
                    description: '',
                    isEnabled: false,
                    creator: '',
                    gmtCreate: '',
                    gmtModified: '',
                };
            },


        },
        created() {
            this.getBaseRegExpList()
        }

    }
</script>
<style>

</style>